import React from 'react'
import s from './ProfilePage.module.css'
import photo from './../../../assets/images/img/IMG_20200131_152849.jpg'
import sharp from './../../../assets/images/icons/Profile/sharp.png'
import css3 from './../../../assets/images/icons/Profile/css3.png'
import docker from './../../../assets/images/icons/Profile/docker.png'
import figma from './../../../assets/images/icons/Profile/figma.png'
import git from './../../../assets/images/icons/Profile/git.png'
import github from './../../../assets/images/icons/Profile/github.png'
import gulp from './../../../assets/images/icons/Profile/gulp.png'
import html5 from './../../../assets/images/icons/Profile/html5.png'
import illustrator from './../../../assets/images/icons/Profile/illustrator.png'
import javascript from './../../../assets/images/icons/Profile/javascript.png'
import jest from './../../../assets/images/icons/Profile/jest.png'
import nodejs from './../../../assets/images/icons/Profile/nodejs.png'
import photoshop from './../../../assets/images/icons/Profile/photoshop.png'
import react from './../../../assets/images/icons/Profile/react.png'
import redux from './../../../assets/images/icons/Profile/redux.png'
import sass from './../../../assets/images/icons/Profile/sass.png'
import sublime from './../../../assets/images/icons/Profile/sublime.png'
import typescript from './../../../assets/images/icons/Profile/typescript.png'
import unity from './../../../assets/images/icons/Profile/unity.png'
import vs_code from './../../../assets/images/icons/Profile/vs_code.png'
import webpack from './../../../assets/images/icons/Profile/webpack.png'

function ProfilePage(props) {
    return (<div className={s.profile}>
        <div className={s.content}>
            <div className={s.title}>
                <div className={s.sidebar}>
                    <div className={s.photo}>
                        <img src={photo} alt="" title="This is I'm" />
                    </div>
                    <div className={s.icons}>
                        <img src={javascript} title="javascript" />
                        <img src={html5} title="html5" />
                        <img src={css3} title="css3" />
                        <img src={react} title="react" />
                        <img src={redux} title="redux" />
                        <img src={typescript} title="typescript" />
                        <img src={sass} title="sass" />
                        <img src={webpack} title="webpack" />
                        <img src={gulp} title="gulp" />
                        <img src={nodejs} title="nodejs" />
                        <img src={jest} title="jest" />
                        <img src={docker} title="docker" />
                        <img src={git} title="git" />
                        <img src={github} title="github" />
                        <img src={sharp} title="sharp" />
                        <img src={unity} title="unity" />
                        <img src={photoshop} title="photoshop" />
                        <img src={illustrator} title="illustrator" />
                        <img src={figma} title="figma" />
                        <img src={vs_code} title="vs_code" />
                        <img src={sublime} title="sublime" />
                    </div>
                </div>
                <div className={s.text}>
                    <div className={s.header}>
                        Обо мне
                    </div>
                    <div className={s.body}>
                        <div className={s.bodyTitle}>
                            Постников Станислав
                            <span>Web-разработчик</span>
                        </div>
                        <div className={s.bodyContent}>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>)
}

export default ProfilePage;