import React from 'react'
import s from './Title.module.css'
import Author from './Author/Author'

function Title(props) {
    return (<>
        <div className={s.title}>
            <span>M</span>
            <span>i</span>
            <span>r</span>
            <span>u</span>
            <span>d</span>
            <span>i</span>
            <span>s</span>
            <span>t</span>
            <span>o</span>
            <span>k</span>
            <Author />
        </div>
    </>)
}

export default Title;