import React from 'react'
import s from './ContactsPage.module.css'
import instagram from './../../../assets/images/icons/Contacts/socialNetworking/instagram.svg'
import linkedin from './../../../assets/images/icons/Contacts/socialNetworking/linkedin.svg'
import telegram from './../../../assets/images/icons/Contacts/socialNetworking/telegram.svg'
import viber from './../../../assets/images/icons/Contacts/socialNetworking/viber.svg'
import vk from './../../../assets/images/icons/Contacts/socialNetworking/vk.svg'
import googlePlus from './../../../assets/images/icons/Contacts/email/googlePlus.svg'
import yandex from './../../../assets/images/icons/Contacts/email/yandex.svg'

function ContactsPage(props) {
    return (<div className={s.contacts}>
        <div className={s.plarform}>
            <div className={s.content}>
                <div className={`${s.item} ${s.socialNetworking}`}>
                    <div className={s.title}>
                        I'm in social networking
                </div>
                    <div className={s.icons}>
                        <div className={s.inner}>
                            <a href="#" title="Instagram">
                                <img src={instagram} alt="" />
                            </a>
                            <a href="#" title="Linkedin">
                                <img src={linkedin} alt="" />
                            </a>
                            <a href="#" title="Telegram">
                                <img src={telegram} alt="" />
                            </a>
                            <a href="#" title="Viber">
                                <img src={viber} alt="" />
                            </a>
                            <a href="#" title="Vk">
                                <img src={vk} alt="" />
                            </a>
                        </div>
                    </div>
                </div>
                <div className={`${s.item} ${s.email}`}>
                    <div className={s.title}>
                        My email adress
                </div>
                    <div className={s.icons}>
                        <div className={s.inner}>
                            <a href="#" title="Google">
                                <img src={googlePlus} alt="" />
                            </a>
                            <a href="#" title="Yandex">
                                <img src={yandex} alt="" />
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>)
}

export default ContactsPage;