import React from 'react'
import s from './Canvas.module.css'
import TitlePage from './TitlePage/TitlePage'
import ProfilePage from './ProfilePage/ProfilePage'
import WorksPage from './WorksPage/WorksPage'
import BlogPage from './BlogPage/BlogPage'
import ContactsPage from './ContactsPage/ContactsPage'
import BlogPageArticle from './BlogPage/BlogPageArticle/BlogPageArticle'
import Error from './Error/Error'
import { Route, Switch } from 'react-router-dom'

function Canvas() {
    return (<div className={s.canvas}>
        <Switch>
            <Route exact path="/" render={() => <TitlePage />} />
            <Route exact path="/home" render={() => <TitlePage />} />
            <Route exact path="/profile" render={() => <ProfilePage />} />
            <Route path="/works" render={() => <WorksPage />} />
            <Route exact path="/blog/" render={() => <BlogPage />} />
            <Route path="/blog/:id" render={() => <BlogPageArticle />} />
            <Route path="/contacts" render={() => <ContactsPage />} />
            <Route render={() => <Error />} />
        </Switch>
    </div>
    )
}

export default Canvas;